import React from 'react';

import '../styles/About.css';

export default () =>
  <div className='about'>
    <h1>Utility Data Viewer</h1>
    <p>See how your energy use and utility bills change through time.</p>
    <p>Navigate to different pages using the header or the arrows on either side.</p>
  </div>;



